* {
    box-sizing: border-box;
    font-family: sans-serif;
  }
  
  html, body, #root {
    padding: 0;
    margin: 0;
    height: 100%;
  }  
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
.navbar {
    padding: 20px;
    font-family: "Cormorant SC";
    box-shadow: 10px 5px 20px 2px rgb(2, 1, 0);
    background-image: linear-gradient(to bottom right, rgb(80, 3, 104), rgb(156, 38, 156));
    position: fixed;
    width: 100%;
    z-index: 99;
    white-space: nowrap;
}
.card {
  width: 200px;
  height: 200px;
  background-size: cover;
  cursor: pointer;
  transition: box-shadow 0.25s, -webkit-transform 0.25s;
  transition: transform 0.25s, box-shadow 0.25s;
  transition: transform 0.25s, box-shadow 0.25s, -webkit-transform 0.25s;
  border: 5px solid #ffffff;
  border-radius: 3px;
}
  
.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  z-index: 2;
  box-shadow: 3px 14px 33px 0px rgba(0,0,0,0.5);
}
  
.footer {
  background-image: linear-gradient(to bottom right, rgb(80, 3, 104), rgb(156, 38, 156));
  padding: 20px;
  color: white;
  font-style: italic;
  box-shadow: 10px 5px 20px 2px rgb(2, 1, 0);
}
